





import Vue from 'vue';
import Component from 'vue-class-component';

@Component({})
export default class Group extends Vue {
  public src: string = '';

  public data() {
    return {
      src: undefined,
    };
  }

  public created(): void {
    this.src =
      'https://groups.google.com/forum/embed/?place=forum/jadzwingow2' +
      '&showsearch=true&showpopout=true&showtabs=false' +
      '&parenturl=' +
      encodeURIComponent(window.location.href);
  }
}
